import React from "react";
import { Link } from "gatsby";
import { dateFormat } from "../components/Excerpt";

const Article = ( {data} ) => {
	return (
	<article className="Article mb-L">
		<h1 className="mb-0">
			<Link to={`/${data.fields.slug}`}>
				{data.frontmatter.title}
			</Link>
		</h1>
		
		<time className="block mb-L">{ dateFormat(`${data.frontmatter.date}`) }</time>
		<div dangerouslySetInnerHTML={{ __html: data.html }} />
	</article>
)}

export default Article

