import React from "react"
import { graphql } from "gatsby"

import Article from "../components/Article"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = ({ data }) => (
  <Layout>
		<div>
			{ data.allMarkdownRemark.edges.map(({ node }) => ( <Article key={ node.id } data={ node } /> )) }
		</div>
	  <SEO title="Home" />
  </Layout>
)


export const query = graphql`
query{
	allMarkdownRemark(
		filter: { 
			frontmatter:{
				date: {ne: null}
				published: {eq: true}
			}
		},
		sort: {
			order: DESC,
			fields: frontmatter___date
		}
		limit: 5
	) {
		totalCount
		edges {
			node {
				id
				frontmatter { 
					title
					date
					category
					description
				}
				fields {
					slug
				}
				excerpt(format: HTML)
				html
			}
		}
	}
}`

export default IndexPage
